import React, { useState } from "react"
import './Login.css';
import { useNavigate } from 'react-router-dom';
import axios from "axios";
import { useDispatch } from "react-redux";
import { Data } from "../../ReduxFloder/action";

const Login = () => {

    const navigate = useNavigate();
    const dispatch = useDispatch();
    const [username, setusername] = useState('')
    const [password, setpassword] = useState('')
    const [error, seterror] = useState(false)
    const [error1, seterror1] = useState(false)
    const [error2, seterror2] = useState('')
    const [error3, seterror3] = useState(false)

    const Dashboard = () => {
        if (username == "") {
            // alert('Please enter your username')
            seterror(true)
            seterror1(false)
        }
        else if (password == "") {
            // alert('Please enter yoyr password')
            seterror1(true)
            seterror(false)
        }
        else {
            seterror(false)
            seterror1(false)
            var mail = username.target.value
            var pass = password.target.value
            let bodyFormData = { 'email': mail, 'password': pass }
            console.log(bodyFormData);
            axios({
                method: 'POST',
                url: `https://qb.flitsync.com/api/login.php`, // Signup API
                data: bodyFormData,
                config: { headers: { 'Content-Type': 'application/json' } }
            })
                .then(({ data, status }) => {
                    console.warn("login api data", JSON.stringify(data))
                    dispatch(Data(data))
                    navigate('/Dashboard')
                    // window.location.reload();
                })
                .catch(error => {
                    // alert(error.response.data.message)
                    seterror2(error.response.data.message)
                    seterror3(true)
                    console.warn("login error", error)
                })
        }
    }

    const ForgetPassword = () => {
        navigate('/forgetpassword')
    }

    return (
        <div className="LoginForm">
            <section class="vh-100"  >
                <div class="container py-5 h-100">
                    <div class="row d-flex justify-content-center align-items-center h-100">
                        <div class="col col-xl-10">
                            <div class="card" style={{ borderRadius: '1rem' }} >
                                <div class="row g-0">
                                    <div class="col-md-6 col-lg-5 d-none d-md-block">
                                        <img src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-login-form/img1.webp"
                                            alt="login form" class="img-fluid" />
                                    </div>
                                    <div class="col-md-6 col-lg-7 d-flex align-items-center">
                                        <div class="card-body p-4 p-lg-5 text-black">
                                            <form>
                                                <div class="d-flex align-items-center mb-3 pb-1">
                                                    <span class="h1 fw-bold mb-0">Demeter Fragrances</span>
                                                </div>
                                                <h5 class="fw-normal mb-3 pb-3" >Sign into your account</h5>
                                                <div class="form-outline mb-4">
                                                    <label class="form-label" for="form2Example17">Email address</label>
                                                    <input type="email" onChange={(email) => { setusername(email) }} id="form2Example17" class="form-control form-control-lg" />
                                                    {error == true &&
                                                        <div className="error">Please Enter Your Email</div>
                                                    }
                                                </div>
                                                <div class="form-outline mb-4">
                                                    <label class="form-label" for="form2Example27">Password</label>
                                                    <input type="password" onChange={(pass) => { setpassword(pass) }} id="form2Example27" class="form-control form-control-lg" />
                                                    {error1 == true &&
                                                        <div className="error">Please Enter Your Password</div>
                                                    }
                                                </div>
                                                {error3 == true &&
                                                    <div className="error">{error2}</div>
                                                }
                                                <div class="pt-1 mb-4">
                                                    <button onClick={Dashboard} class="btn btn-dark btn-lg btn-block" type="button">Login</button>
                                                </div>
                                                <a onClick={ForgetPassword} class="small text-muted" href="/ForgetPassword">Forgot password?</a>
                                                <p class="mb-5 pb-lg-2" style={{ color: '#393f81' }} > <a href="#!"
                                                ></a></p>
                                                <a href="#!" class="small text-muted">Terms of use.</a>
                                                <a href="#!" class="small text-muted">Privacy policy</a>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default Login